@font-face
{
    font-family: 'Cuprum';
    font-weight: 400;
    font-style: normal;

    font-display: swap;
    src: local('Cuprum Regular'), local('Cuprum-Regular'), url('../fonts/cuprum.woff2') format('woff2'), local('Cuprum Regular'), local('Cuprum-Regular'), url('../fonts/cuprum.woff') format('woff');
}

@font-face
{
    font-family: 'Cuprum';
    font-weight: 400;
    font-style: italic;

    font-display: swap;
    src: local('Cuprum Italic'), local('Cuprum-Italic'), url('../fonts/cuprumitalic.woff2') format('woff2'), local('Cuprum Italic'), local('Cuprum-Italic'), url('../fonts/cuprumitalic.woff') format('woff');
}

@font-face
{
    font-family: 'Cuprum';
    font-weight: 700;
    font-style: normal;

    font-display: swap;
    src: local('Cuprum Bold'), local('Cuprum-Bold'), url('../fonts/cuprumbold.woff2') format('woff2'), local('Cuprum Bold'), local('Cuprum-Bold'), url('../fonts/cuprumbold.woff') format('woff');
}

@font-face
{
    font-family: 'PT Sans';
    font-weight: 400;
    font-style: normal;

    font-display: swap;
    src: local('PT Sans'), local('PTSans-Regular'), url('../fonts/ptsans.woff2') format('woff2'), local('PT Sans'), local('PTSans-Regular'), url('../fonts/ptsans.woff') format('woff');
}

@font-face
{
    font-family: 'PT Sans';
    font-weight: 700;
    font-style: normal;

    font-display: swap;
    src: local('PT Sans Bold'), local('PTSans-Bold'), url('../fonts/ptsansbold.woff2') format('woff2'), local('PT Sans Bold'), local('PTSans-Bold'), url('../fonts/ptsansbold.woff') format('woff');
}

:root
{
    --color-black-01: #000;
    --color-black-02: rgb(30, 30, 30);
    --color-black-a-01: rgba(0, 0, 0, 0);
    --color-black-a-02: rgba(0, 0, 0, .15);
    --color-black-a-03: rgba(0, 0, 0, .25);
    --color-black-a-04: rgba(0, 0, 0, .5);
    --color-black-a-05: rgba(0, 0, 0, .3);
    --color-white-01: #fff;
    --color-white-02: #f9f5f0;
    --color-white-03: #f3f7f8;
    --color-white-04: #f1f5f7;
    --color-white-05: #c5c5c5;
    --color-white-06: #a9a9a9;
    --color-white-07: #f1f1f1;
    --color-white-08: #e5e5e5;
    --color-white-09: #544a3e;
    --color-white-10: #d7dcde;
    --color-white-11: #ababab;
    --color-white-12: #cfcfcf;
    --color-white-13: #b5b5b5;
    --color-white-14: #e9e9e9;
    --color-white-a-01: rgba(255, 255, 255, 1.0);
    --color-white-a-02: rgba(255, 255, 255, .5);
    --color-white-a-03: rgba(255, 255, 255, .3);
    --color-white-a-04: hsla(0, 0%, 100%, .3);
    --color-white-a-05: hsla(0, 0%, 77%, .3);
    --color-white-a-06: hsla(0, 0%, 66%, .3);
    --color-red-01: #ee3643;
    --color-red-02: #ca2c37;
    --color-red-03: #ba2732;
    --color-red-04: #b52933;
    --color-green-01: #8ed78f;
    --color-green-02: #63a63e;
    --color-green-03: #5fbb2d;
    --color-green-04: #518534;
    --color-green-05: #367315;
    --color-green-06: #00ca74;
    --color-blue-01: #3bbce3;
    --color-blue-02: #405069;
    --color-blue-03: #32425c;
    --color-blue-04: #293449;
    --color-blue-05: #212a3a;
    --color-blue-06: #1d2739;
    --color-blue-07: #161d29;
    --color-blue-08: #3d546f;
    --color-blue-a-01: rgba(41, 52, 73, .5);
    --color-yellow-01: #ffd180;
    --color-yellow-02: #ffc047;
    --color-yellow-03: #ffbf47;
    --color-yellow-04: #f7f3ec;
    --color-purple-01: #dc91d8;
}

img
{
    max-width: 100%;
    height: auto;
}

.body
{
    font-family: 'Cuprum', 'Tahoma', sans-serif;
    font-weight: 400;
    line-height: 1.15em;

    display: grid;

    min-width: 970px;
    min-height: 100vh;

    color: var(--color-black-01);
    background-color: var(--color-white-01); 

    grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
    grid-template-rows:         min-content 1fr min-content;
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden
{
    position: absolute;

    overflow: hidden; 
    clip: rect(0 0 0 0);

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;

    white-space: nowrap;

    border: 0;

    -webkit-clip-path: inset(100%);
            clip-path: inset(100%);
}

.container
{
    max-width: 940px;
    margin: 0 auto;
    padding: 0 15px;
}

.btn
{
    font-size: 14px;

    display: inline-block;

    box-sizing: border-box;
    padding: 10px;

    cursor: pointer; 
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    text-transform: uppercase;

    color: var(--color-white-01);
    border: none;
    background-color: var(--color-red-01);
}
.btn:hover,
.btn:focus
{
    background-color: var(--color-red-02);
}
.btn:active
{
    background-color: var(--color-red-03);
}

.btn--light
{
    color: var(--color-black-01);
    background-color: var(--color-white-01);
}
.btn--light:hover,
.btn--light:focus
{
    color: var(--color-red-01);
    background-color: var(--color-white-01);
}
.btn--light:active
{
    color: var(--color-white-05);
}

.btn-logo
{
    display: flex;

    width: 220px;
    min-height: 42px;

    cursor: pointer; 

    color: var(--color-white-01);
    background-color: var(--color-red-01);
    box-shadow: 0 5px 0 var(--color-red-04);

    justify-content: center;
    align-items: center;
}
.btn-logo:hover:not(.btn-logo--active),
.btn-logo:focus:not(.btn-logo--active)
{
    background-color: var(--color-red-02);
}
.btn-logo:active:not(.btn-logo--active)
{
    background-color: var(--color-red-03);
}
.btn-logo--active
{
    cursor: default;
}

.btn-buy
{
    font-size: 14px;

    position: relative;

    display: block;

    box-sizing: border-box;
    width: 135px;
    padding: 10px;
    padding-left: 20px;

    text-align: center;
    text-decoration: none;
    text-transform: uppercase;

    color: var(--color-white-01);
    border-radius: 2px;
    background-color: var(--color-green-02); 
    box-shadow: inset 0 -3px 0 var(--color-green-05);
}
.btn-buy:hover,
.btn-buy:focus
{
    background-color: var(--color-green-03);
}
.btn-buy:active
{
    background-color: var(--color-green-04);
    box-shadow: none;
}
.btn-buy::before
{
    position: absolute;
    top: 10px;
    left: 29px;

    content: url('../img/common__icon-basket.svg');

    opacity: .3;
}

.btn-bookmark
{
    font-size: 14px;

    display: block;

    box-sizing: border-box;
    width: 135px;
    padding: 7px;

    text-align: center; 
    text-decoration: none;
    text-transform: uppercase;

    color: var(--color-blue-03);
    border: 3px solid var(--color-green-02);
    border-radius: 2px;
}
.btn-bookmark:hover,
.btn-bookmark:focus
{
    border: 3px solid var(--color-blue-03);
}
.btn-bookmark:active
{
    opacity: .5;
}

.btn-price
{
    font-family: 'PT Sans', 'Arial', sans-serif;
    font-size: 17px;
    font-weight: 700;

    display: inline-block;

    box-sizing: border-box;
    width: 141px;
    padding: 10px;
    padding-left: 0;

    vertical-align: bottom;
    text-decoration: none;
    text-transform: uppercase;

    color: var(--color-white-01);
    background-image: url('../img/common__red-button.svg');
}

.btn-services
{
    font-size: 21px;
    font-weight: 700;

    display: block;

    box-sizing: border-box;
    padding: 18px 22px 20px;

    cursor: pointer;
    text-align: start;
    text-transform: none;

    color: var(--color-white-01);
    border: none; 
    background-color: var(--color-blue-03);
    box-shadow: inset 0 1px 0 var(--color-blue-04), inset 0 2px 0 var(--color-blue-02);
}
.btn-services:hover,
.btn-services:focus
{
    color: var(--color-white-01);
    background-color: var(--color-blue-04);
}
.btn-services:active
{
    color: var(--color-white-a-02);
    background-color: var(--color-blue-06);
}
.btn-services--active
{
    color: var(--color-blue-03);
    background-color: var(--color-white-01);
    box-shadow: none;
}
.btn-services--active:hover,
.btn-services--active:focus
{
    color: var(--color-blue-03);
    background-color: var(--color-white-01);
}

.user-controls
{
    background-color: var(--color-blue-04);
}

.user-controls__container
{
    display: flex;
}

.user-controls__search-form
{
    display: flex;

    flex-grow: 1;
}
.user-controls__search-form:hover,
.user-controls__search-form:focus
{
    background-color: var(--color-blue-05);
}
.user-controls__search-form:hover .user-controls__search-btn,
.user-controls__search-form:focus .user-controls__search-btn
{
    color: var(--color-white-a-01);
}
.user-controls__search-form:focus-within
{
    background-color: var(--color-white-01);
}
.user-controls__search-form:focus-within > .user-controls__search-btn
{
    color: var(--color-red-01);
}
.user-controls__search-form:focus-within > .user-controls__search-input
{
    color: var(--color-black-01);
}

.user-controls__search-btn
{
    width: 28px;
    margin-right: 2px;
    margin-left: 10px;
    padding-top: 4px;

    color: var(--color-white-a-03);
    border: none; 
    background-color: inherit;
}

.user-controls__search-input
{
    font-size: 18px;
    line-height: 1.35em;

    width: 150px;

    color: var(--color-white-01);
    border: none; 
    background-color: inherit;

    flex-grow: 1;
}
.user-controls__search-input::-moz-placeholder
{
    color: var(--color-white-01);
}
.user-controls__search-input:-ms-input-placeholder
{
    color: var(--color-white-01);
}
.user-controls__search-input::-ms-input-placeholder
{
    color: var(--color-white-01);
}
.user-controls__search-input::placeholder
{
    color: var(--color-white-01);
}

.user-controls__buttons
{
    display: grid;

    margin-left: auto;

    grid-template-columns: repeat(3, 1fr);
}

.user-controls__bookmarks
{
    margin-left: auto;
}

.btn-user-controls
{
    font-size: 18px;
    line-height: 1.33em; 

    position: relative;

    box-sizing: border-box;
    min-width: 150px;
    padding: 9px 0;

    text-align: center;
    text-decoration: none;

    color: var(--color-white-01);
}
.btn-user-controls:hover,
.btn-user-controls:focus
{
    background-color: var(--color-blue-05);
}
.btn-user-controls:hover::before,
.btn-user-controls:focus::before
{
    opacity: 1.0;
}
.btn-user-controls:active
{
    color: var(--color-white-a-02);
    background-color: var(--color-blue-07);
}
.btn-user-controls:active::before
{
    opacity: .3;
}
.btn-user-controls--bookmark
{
    padding-left: 44px;

    text-align: left;
}
.btn-user-controls--bookmark::before
{
    position: absolute;
    top: 12px;
    left: 20px;

    content: url('../img/icon-bookmark.svg');

    opacity: .3;
}
.btn-user-controls--basket
{
    padding-left: 49px;

    text-align: left;
}
.btn-user-controls--basket::before
{
    position: absolute;
    top: 11px;
    left: 26px;

    content: url('../img/icon-basket.svg');

    opacity: .3;
}
.btn-user-controls--checkout
{
    background-color: var(--color-green-02);
}
.btn-user-controls--checkout:hover,
.btn-user-controls--checkout:focus
{
    background-color: var(--color-green-03);
}
.btn-user-controls--checkout:active
{
    color: var(--color-white-a-02);
    background-color: var(--color-green-02);
}
.btn-user-controls--not-zero
{
    background-color: var(--color-red-01);
}
.btn-user-controls--not-zero:hover,
.btn-user-controls--not-zero:focus
{
    background-color: var(--color-red-01);
}
.btn-user-controls--not-zero:active
{
    color: var(--color-white-a-02);
    background-color: var(--color-red-03);
}

.title-bar
{
    display: flex;

    box-sizing: border-box;
    min-height: 89px;
    padding: 22px;
    padding-right: 14px;
    padding-left: 28px;

    background-color: var(--color-white-02); 

    justify-content: space-between;
    align-items: center;
}

.title-bar__title
{
    font-size: 30px;
    font-weight: 400;
    line-height: 1.2em;

    margin: 0;

    text-transform: uppercase; 

    color: var(--color-blue-03);
}

.title-bar__button
{
    min-width: 192px;
}

.header
{
    background-color: var(--color-white-03);
}

.header__container
{
    display: flex;
}

.header__title
{
    font-size: 16px;
    font-weight: 400;
    font-style: italic; 
    line-height: 1.5em;

    width: 200px;
    margin: 0;
    margin-right: 60px;
    padding: 43px 30px 43px 30px;

    color: var(--color-red-01);
}

.header__container-phone-address
{
    width: 270px;
    margin-top: 46px;
    margin-right: 10px;
}

.header__phone
{
    font-size: 21px;
    font-weight: 700;

    position: relative;

    display: block;

    box-sizing: border-box;
    margin-bottom: 3px;
    padding: 10px 0 11px 30px;

    text-align: center;
    text-decoration: none; 

    color: var(--color-red-01);
    border: 3px solid var(--color-white-01);
}
.header__phone::before
{
    position: absolute;
    top: 10px;
    left: 36px; 

    content: url('../img/user-controls__icon-phone.svg');
}

.header__address
{
    font-size: 14px;
    font-style: normal;
    line-height: 1.72em;

    text-align: center; 

    color: var(--color-blue-03);
}

.header__user-nav
{
    max-width: 330px; 
    margin-top: 46px;
    margin-left: auto;
}

.user-nav__controls
{
    display: none;
}
.user-nav__controls--visible
{
    display: block;
}

.user-nav__buttons
{
    display: flex;

    margin: 0;
    padding: 0;

    list-style: none; 

    flex-wrap: wrap;
    justify-content: flex-end;
}
.user-nav__buttons--solid
{
    background-color: var(--color-white-01);
}

.user-nav__login
{
    margin-right: 10px;
}

.user-nav__profile
{
    margin-right: 2px;
}

.user-nav__logged-list
{
    display: flex;

    margin: 0;
    padding: 0;

    list-style: none; 

    justify-content: center;
}

.btn-user-nav
{
    font-size: 21px;

    position: relative;

    display: block;

    box-sizing: border-box;
    min-height: 45px;
    padding: 13px 27px;

    text-align: center;
    text-decoration: none;

    color: var(--color-black-01);
    background-color: var(--color-white-01);
}
.btn-user-nav:hover,
.btn-user-nav:focus
{
    color: var(--color-red-01);
}
.btn-user-nav:hover .btn-user-nav__icon,
.btn-user-nav:focus .btn-user-nav__icon
{
    color: var(--color-blue-08);
}
.btn-user-nav:active
{
    color: var(--color-white-05);
}
.btn-user-nav:active .btn-user-nav__icon
{
    color: var(--color-white-05);
}
.btn-user-nav--login
{
    padding-right: 27px; 
    padding-left: 46px;
}
.btn-user-nav--register
{
    padding-right: 24px; 
    padding-left: 24px;
}
.btn-user-nav--profile
{
    padding-right: 7px; 
    padding-left: 56px;
}
.btn-user-nav--logout
{
    padding-right: 24px; 
    padding-left: 24px;
}

.btn-user-nav__icon
{
    position: absolute;
    top: 0;
    left: 0;

    color: var(--color-white-05);
}
.btn-user-nav__icon--login
{
    top: 14px;
    left: 19px;
}
.btn-user-nav__icon--profile
{
    top: 14px;
    left: 15px;
}
.btn-user-nav__icon--logout
{
    top: 14px;
    left: 11px;
}

.site-nav
{
    padding-bottom: 60px;
}

.site-nav__list
{
    display: flex;

    margin: 0;
    padding: 0;
    padding-right: 19px;
    padding-left: 24px;

    list-style: none; 

    background-color: var(--color-blue-03);
    box-shadow: inset 0 -5px 0 var(--color-blue-04);

    flex-wrap: wrap;
}

.site-nav__item
{
    text-align: center; 

    flex-grow: 1;
}

.site-nav__link
{
    font-size: 18px;
    line-height: 1.35em;

    display: block;

    padding: 17px 10px 19px;

    text-decoration: none; 

    color: var(--color-white-01);
}
.site-nav__link:hover:not(.site-nav__link--active),
.site-nav__link:focus:not(.site-nav__link--active)
{
    background-color: var(--color-blue-04);
}
.site-nav__link:active:not(.site-nav__link--active)
{
    color: var(--color-white-a-02);
    background-color: var(--color-blue-06);
}

.link-user-nav
{
    font-size: 16px;

    position: relative;

    display: block;

    box-sizing: border-box;
    padding: 7px 0;

    text-decoration: none; 

    color: var(--color-blue-03);
}
.link-user-nav:hover,
.link-user-nav:focus
{
    color: var(--color-red-01);
}
.link-user-nav:active
{
    color: var(--color-white-05);
}
.link-user-nav--circle
{
    margin-right: 33px;
}
.link-user-nav--circle::after
{
    position: absolute;
    top: 14px;
    right: -19px;

    width: 4px;
    height: 4px;

    content: '';
    cursor: default; 
    pointer-events: none;

    border-radius: 50%;
    background-color: var(--color-blue-03);
}

.features
{
    margin-top: 59px;
    margin-bottom: 59px;
}
.features--hidden
{
    display: none;
}

.features__layout
{
    display: grid;

    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.features__slider
{
    grid-column: 1 / 3;
    grid-row: 2 / 4;
}

.promo-cart
{
    position: relative;

    box-sizing: border-box;
    min-width: 300px;
    padding-top: 21px;
    padding-right: 21px;
    padding-bottom: 21px;
    padding-left: 23px;

    color: var(--color-white-01);
    background-color: var(--color-yellow-03);
    background-repeat: no-repeat;
}
.promo-cart--new::after
{
    position: absolute;
    top: 0; 
    right: 0;

    content: url('../img/common__flag-new.svg');
}
.promo-cart--materials
{
    background-color: var(--color-yellow-03);
    background-image: url('../img/promo-cart--materials.svg');
    background-position: 213px 31px;
}
.promo-cart--instrument
{
    background-color: var(--color-blue-01);
    background-image: url('../img/promo-cart--instrument.svg');
    background-position: 194px 34px;
}
.promo-cart--technics
{
    background-color: var(--color-purple-01);
    background-image: url('../img/promo-cart--technics.svg');
    background-position: 191px 31px;
}
.promo-cart--discount
{
    background-color: var(--color-green-01);
    background-image: url('../img/promo-cart--discount.svg');
    background-position: 196px 26px;
}
.promo-cart--delivery
{
    background-color: var(--color-yellow-02);
    background-image: url('../img/promo-cart--delivery.svg');
    background-position: 191px 32px;
}

.promo-cart__title
{
    font-size: 24px;
    font-weight: 700;
    line-height: 1.25em; 

    margin: 0;
    padding: 0;
    padding-bottom: 13px;

    color: var(--color-white-01);
}

.promo-cart__link
{
    font-size: 14px;
    line-height: 1.3em;

    display: block;

    box-sizing: border-box;
    max-width: 135px;
    padding: 10px 0;

    text-align: center;
    text-decoration: none;
    text-transform: uppercase;

    color: var(--color-white-01);
    background-color: var(--color-white-a-04);
}
.promo-cart__link:hover,
.promo-cart__link:focus
{
    background-color: var(--color-white-a-05);
}
.promo-cart__link:active
{
    background-color: var(--color-white-a-06);
}

.promo-sliders
{
    position: relative;

    width: 620px;
}

.promo-sliders__list
{
    margin: 0;
    padding: 0;

    list-style: none;
}

.promo-sliders__btn-prev,
.promo-sliders__btn-next
{
    position: absolute;
    top: 112px;

    width: 42px;
    height: 60px;
    padding: 0;

    cursor: pointer;

    border: none;
    background-color: transparent;
    background-repeat: no-repeat;
