.modal-bg
{
    position: fixed;

    visibility: hidden;

    width: 100%;
    height: 100%;

    transition: opacity .5s cubic-bezier(.22, .61, .36, 1); 

    opacity: 0;
    background-color: var(--color-black-02);
}
.modal-bg--visible
{
    visibility: visible;

    opacity: .8;
}

.modal-container
{
    position: fixed;
    top: 0;
    left: 0;

    display: none;

    width: 100%;
    height: 100%;

    justify-content: center;
    align-items: center;
}
.modal-container--visible
{
    display: flex;
}
.modal-container--bounce
{
    -webkit-animation: bounce .6s;
            animation: bounce .6s;
}
.modal-container--shake
{
    -webkit-animation: shake .6s;
            animation: shake .6s;
}

@-webkit-keyframes bounce
{
    0%
    {
        transform: translateY(-2000px);
    }
    70%
    {
        transform: translateY(30px);
    }
    90%
    {
        transform: translateY(-10px);
    }
    100%
    {
        transform: translateY(0);
    }
}

@keyframes bounce
{
    0%
    {
        transform: translateY(-2000px);
    }
    70%
    {
        transform: translateY(30px);
    }
    90%
    {
        transform: translateY(-10px);
    }
    100%
    {
        transform: translateY(0);
    }
}

@-webkit-keyframes shake
{
    0%,
    100%
    {
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70%,
    90%
    {
        transform: translateX(-10px);
    }
    20%,
    40%,
    60%,
    80%
    {
        transform: translateX(10px);
    }
}

@keyframes shake
{
    0%,
    100%
    {
        transform: translateX(0);
    }
    10%,
    30%,
    50%,
    70%,
    90%
    {
        transform: translateX(-10px);
    }
    20%,
    40%,
    60%,
    80%
    {
        transform: translateX(10px);
    }
}

@keyframes bounce
{
    0%
    {
        transform: translateY(-2000px);
    }
    70%
    {
        transform: translateY(30px);
    }
    90%
    {
        transform: translateY(-10px);
    }
    100%
    {
        transform: translateY(0);
    }
}

